import React, { Component } from "react";
import Typical from "react-typical";
import Switch from "react-switch";
import About from "./About";
import bgimage from './profile.jpg'
import './animate.css'
import './bootstrap.css'
import './flexslider.css'
import './style.css'
class Header extends Component {
  titles = [];

  constructor() {
    super();
    this.state = { checked: false };
    this.onThemeSwitchChange = this.onThemeSwitchChange.bind(this);
  }

  onThemeSwitchChange(checked) {
    this.setState({ checked });
    this.setTheme();
  }

  setTheme() {
    var dataThemeAttribute = "data-theme";
    var body = document.body;
    var newTheme =
      body.getAttribute(dataThemeAttribute) === "dark" ? "light" : "dark";
    body.setAttribute(dataThemeAttribute, newTheme);
  }

  render() {
    if (this.props.sharedData) {
      var name = this.props.sharedData.name;
      this.titles = this.props.sharedData.titles.map(x => [x.toUpperCase(), 1500]).flat();
    }

    const HeaderTitleTypeAnimation = React.memo(() => {
      return <Typical className="title-styles" steps={this.titles} loop={50}  />
    }, (props, prevProp) => true);

    return (
      <>
        {/* <About></About> */}
        <section class="site-hero" style={{
          backgroundImage: `url(${bgimage})`
        }} id="section-home" data-stellar-background-ratio="0.5">
          <div className="col-md-12">
            <div className="row center mx-auto mb-5">
              <div className="container">
                <div className="row intro-text align-items-center justify-content-center">
                  <div className="col-md-10 text-center pt-5">
                    <h1 className="site-heading site-animate">Hello, I'm <strong class="d-block">Abdul Azim Shaikh</strong></h1>
                    <div className="title-container">
                      <HeaderTitleTypeAnimation />
                    </div>
                    <Switch
                      checked={this.state.checked}
                      onChange={this.onThemeSwitchChange}
                      offColor="#baaa80"
                      onColor="#353535"
                      className="react-switch mx-auto"
                      width={90}
                      height={40}
                      uncheckedIcon={
                        <span
                          className="iconify"
                          data-icon="twemoji:owl"
                          data-inline="false"
                          style={{
                            display: "block",
                            height: "100%",
                            fontSize: 25,
                            textAlign: "end",
                            marginLeft: "20px",
                            color: "#353239",
                          }}
                        ></span>
                      }
                      checkedIcon={
                        <span
                          className="iconify"
                          data-icon="noto-v1:sun-with-face"
                          data-inline="false"
                          style={{
                            display: "block",
                            height: "100%",
                            fontSize: 25,
                            textAlign: "end",
                            marginLeft: "10px",
                            color: "#353239",
                          }}
                        ></span>
                      }
                      id="icon-switch"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* <header id="home" style={{ height: window.innerHeight - 140, display: 'block' }}>
            <div className="row aligner" style={{ height: '100%' }}>
              <div className="col-md-12">
              </div>
            </div>
          </header> */}
        </section>
      </>
    );
  }
}

export default Header;
